* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  --scrollbarBG: #1f2235;
  --thumbBG: #ff4a57;
}

body::-webkit-scrollbar {
  width: 13px;
}

body {
  width: 100%;
  height: 100vh;
  background-color: #1f2235;
  scrollbar-width: thin;
  scrollbar-color: var(--thumbBG) var(--scrollbarBG);
}

body::-webkit-scrollbar-track {
  /* box-shadow: inset 0 0 5px #1f2235b6; */
  background: var(--scrollbarBG);
}

body::-webkit-scrollbar-thumb {
  background-color: var(--thumbBG);
  border-radius: 6px;
  border: 3px solid var(--scrollbarBG);
}

.main-container {
  width: 100%;
  height: 100%;
}

.alignCenter {
  align-items: center;
}
.justifyConter {
  justify-content: center;
}

/* Nav */

.fa,
.fas {
  font-weight: 900;
  color: white;
  margin: auto 7px;
}

.nav-container {
  position: fixed;
  top: 0;
  z-index: 1;
  display: flex;
  background: #1f2235;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding-left: 100px;
  padding-right: 100px;
}

.nav-container.active {
  background: #1f2235;
  box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.1);
  border-radius: 2px;
}

.logo {
  font-size: 1.8rem;
  color: #ff4a57;
  cursor: pointer;
  width: 230px;
}

.logo img {
  width: 65%;
}

.link {
  padding-top: 5px;
  list-style-type: none;
  float: left;
}

.nav-links {
  margin-bottom: 10px;
}

.nav-links .anchor {
  display: inline-block;
  color: #fff;
  text-decoration: none;
  cursor: pointer;
  font-family: "Varela Round", sans-serif;
  font-size: 1rem;
  font-weight: 400;
  line-height: 5.1vh;
  margin: 0.8rem;
}

.nav-links .anchor:hover {
  color: #ff4a57;
}

.anchor.active {
  color: #ff4a57;
  font-weight: 800;
  border-bottom: 2px solid #ff4a57;
}

.menu-icon {
  display: none;
}

/* Home */
.home-container {
  font-family: "Varela Round", sans-serif;
  display: flex;
  color: white;
  flex-direction: row;
  flex-wrap: nowrap;
  align-content: center;
  justify-content: space-evenly;
  background-color: #1f2235;
  height: 100%;
  align-items: center;
  width: 100%;
  background: url("./images/bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  overflow: hidden;
}

.content-container {
  margin-left: 30px;
  width: 50vw;
  height: 100%;
  flex-direction: column;
  display: flex;
  justify-content: center;
}

.main-content {
  font-size: 2.6rem;
}

.skill-animation {
  margin-bottom: 15px;
}

.skill-content {
  color: #9c72be;
  font-family: "Varela Round", sans-serif;
  font-weight: 400;
  font-size: 1.5rem;
}

.fTWtKs {
  width: 140px !important;
}

.cIKpxU {
  width: 140px !important;
}

.hand {
  display: inline-block;
  margin: 0.9rem;
  cursor: pointer;
}

.welcome-content {
  color: #3d3f58;
  font-size: 16px;
  margin-bottom: 20px;
  font-family: "Varela Round", sans-serif;
}

.svg-container {
  margin-bottom: 0;
  margin-top: 90px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.svg {
  width: 100%;
}

/* Sticky Side Nav */

.sticky-icon-container {
  position: fixed;
  bottom: 0px;
  right: 30px;
}

.sticky-icon {
  margin: 20% 2%;
  font-size: 25px;
  color: #3d3f58;
  transition: 0.6s ease;
}

.sticky-icon:hover {
  color: #ff4a57;
  font-size: 27px;
}

.line {
  width: 1px;
  background-color: #3d3f58;
  height: 100px;
  margin-top: 10px;
  margin-left: 10px;
}

/* About */

.about {
  font-family: "Varela Round", sans-serif;
}

.common {
  width: 60%;
  margin: 20px auto;
  text-align: center;
}

.mainHeader {
  font-size: 32px;
  font-weight: 500;
  margin-bottom: 20px;
  color: #fff;
}

.mainContent {
  font-size: 16px;
  color: #dfdfdf;
}
.commonBorder {
  background: #ff4a57;
  height: 3px;
  width: 150px;
  margin: 30px auto;
}

.about {
  width: 100%;
  background: #23263a;
  padding-top: 50px;
}

.about-img {
  display: flex;
  align-items: center;
  justify-content: center;
}

.about-img img {
  width: 350px;
  height: 525px;
}

.info {
  color: #dfdfdf;
  text-align: justify;
  padding-bottom: 40px;
  line-height: 1.8rem;
}
.info h3 {
  margin-top: 30px;
  margin-bottom: 20px;
}
.info-p1 {
  font-size: 16px;
  margin-bottom: 20px;
}
.info-p2 {
  font-size: 16px;
}

@media screen and (max-width: 576px) {
  .common {
    width: 90%;
  }

  .about-img img {
    width: 267px;
    height: 400px;
  }
}

/* Services */

.service {
  padding: 8rem 0;
  background-color: #f9f9ff;
}
.serviceHeader h2 {
  font-size: 2.5rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-bottom: 1rem;
}

.serviceHeader p {
  max-width: 45rem;
  text-align: center;
  margin-bottom: 5rem;
  color: #777777;
  line-height: 26px;
}

.singleService {
  max-width: 30% !important;
  margin: 1.1rem;
  padding: 2rem !important;
  transition: 0.6s ease;
  background-color: #272a3f;
  border-radius: 10px;
}
.singleService:hover {
  box-shadow: 0px 10px 30px 0px rgba(0, 0, 0, 0.08);
}
.singleService h4 {
  color: rgba(255, 255, 255, 0.9);
  font-size: 21px;
  text-transform: uppercase;
  margin: 1rem 0;
}
.singleService p {
  color: #b3bad3;
  line-height: 26px;
  font-size: 16px;
  /* text-align: justify; */
}
.singleService:hover svg.svg-inline--fa.fa-laptop-code.fa-w-20,
.singleService:hover svg.svg-inline--fa.fa-desktop.fa-w-18,
.singleService:hover svg.svg-inline--fa.fa-laptop-house.fa-w-20 {
  color: #ff4a57;
}
svg.svg-inline--fa.fa-laptop-code.fa-w-20,
svg.svg-inline--fa.fa-desktop.fa-w-18,
svg.svg-inline--fa.fa-laptop-house.fa-w-20 {
  font-size: 3rem;
  margin-bottom: 1rem;
  color: rgb(173, 165, 165);
  transition: 0.6s ease;
}

@media screen and (max-width: 576px) {
  .service {
    padding: 2rem;
  }
  .serviceHeader h2 {
    font-size: 2rem;
    text-align: center;
  }
  .singleService {
    max-width: 85% !important;
    margin-left: 30px;
  }
}
@media screen and (min-width: 576px) and (max-width: 768px) {
  .singleService {
    max-width: 95% !important;
  }
  .card-deck {
    flex-direction: column !important;
  }
  .p-4.card {
    margin-bottom: 2rem;
  }
}
@media screen and (min-width: 768px) and (max-width: 992px) {
  .row.serviceRow {
    flex-direction: column;
  }
  .col-md-4.singleService {
    max-width: 95% !important;
  }
}

/* Skills */

.skills-container {
  font-family: "Exo 2", sans-serif;
  color: #fff;
  background-color: #1f2235;
  align-items: center;
  width: 100%;
  min-height: 120vh;
  display: flex;
  flex-direction: column;
  padding: 0 10px;
}

.skills-container h3 {
  border: 1px solid #ff4a57;
  display: inline-block;
  padding: 6px 13px 8px 18px;
  font-size: 22px;
  font-weight: 500;
  text-transform: uppercase;
  border-radius: 5px;
  letter-spacing: 0.5rem;
}

.skills-container img {
  display: flex;
  align-items: center;
}

.skills-container figcaption {
  font-family: "Varela Round", sans-serif;
  font-size: 1rem;
  font-weight: 500;
}

/* Portfolio */
.portfolio-container {
  font-family: "Exo 2", sans-serif;
  color: #fff;
  background-color: #1f2235;
  align-items: center;
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.project-button-container {
  width: 100%;
  margin: auto;
}

.project-button-container button {
  background-color: transparent !important;
  color: #ff4a57 !important;
  padding: 1% 1%;
  border: 0px;
  border-radius: 0px !important;
  font-family: "Exo 2", sans-serif;
  font-size: 22px;
  font-weight: 500;
}

.project-button-container button:focus {
  background-color: transparent;
  border-bottom: 3px solid #ff4a57;
  outline: 0px;
}

.project-button-container button:hover {
  border: 1px solid #ff4a57;
  display: inline-block;
  padding: 5px 15px;
  border-radius: 5px;
}

@media only screen and (max-width: 430px) {
  .project-button-container {
    width: 100%;
    margin: auto;
  }
}

/* Single Work */

.single-work {
  background-color: #272a3f !important;
  padding: 1%;
  min-height: 480px;
  border-radius: 10px;
  transition: transform 0.5s ease-in-out;
}

.single-work:hover .work-img {
  transform: translateY(-80%);
}

.work-img-container {
  width: 100%;
  height: 220px;
  overflow: hidden;
  padding: 8px;
}

.work-img-container:hover .work-img {
  transform: translateY(-80%);
}

.work-img {
  width: 100%;
  transition: transform 2s ease-in-out;
}

.project-name {
  font-family: "Varela Round", sans-serif;
  font-size: 22px;
  font-weight: 500;
}

.project-description {
  font-family: "Varela Round", sans-serif;
  font-size: 14px;
  color: #b3bad3;
}

.work-icon {
  font-size: 25px;
  color: rgb(173, 165, 165);
  margin: 1px 10px;
}

.work-icon:hover {
  color: #ff4a57;
}

.badge {
  color: #fff !important;
  background-color: #ff4a57 !important;
  padding: 0.2em 0.3em;
  margin-left: 5px;
}

.technologies {
  position: relative;
  color: rgba(255, 255, 255, 0.699);
  border: 1px solid #ff4a56d0;
  display: inline-block;
  padding: 5px 15px;
  font-size: 14px;
  font-weight: 400;
  border-radius: 5px;
  letter-spacing: 0.07rem;
}

/* Achievements */

.projects-container {
  width: 100%;
  flex-wrap: wrap;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.projects {
  height: 23rem;
  width: 21rem;
  margin: 1rem;
  cursor: pointer;
  border-radius: 10px;
}

.project-images {
  border-radius: 10px;
  cursor: pointer;
  width: 100%;
  height: 70%;
}

.achievements-container {
  display: flex;
  color: rgba(255, 255, 255, 0.9);
  flex-direction: column;
  padding: 2rem;
  align-items: center;
  justify-content: space-evenly;
  width: 100%;
  min-height: 100vh;
}

.Achievement-images {
  width: 100%;
  height: 15rem;
  background-size: cover;
  background-repeat: no-repeat;
}

.project-links h3 {
  font-family: "Exo 2", sans-serif;
  font-weight: 400;
  font-size: 22px;
}

.flip-box {
  background-color: transparent;
  height: 16rem;
  width: 21rem;
  perspective: 1000px;
}

.flip-box-inner {
  position: absolute;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.8s;
  transform-style: preserve-3d;
}

.flip-box:hover .flip-box-inner {
  transform: rotateY(180deg);
}

.flip-box-front,
.flip-box-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.flip-box-front {
  background-color: transparent;
}

.flip-box-back {
  background-color: transparent;
  transform: rotateY(180deg);
}

/* Education */

.experienceDetails {
  width: 100%;
}

.experience {
  padding: 6rem 0;
}

.main-line {
  min-height: 30rem;
  border-left: 5px solid rgba(255, 255, 255, 0.25);
  z-index: 5;
}
.main-line.col {
  max-width: 1.1rem !important;
}

.years p {
  color: rgba(255, 255, 255, 0.8);
  font-family: "Varela Round", sans-serif;
  font-size: 16px;
}
.years .firstP::after,
.secondP::after,
.thirdP::after {
  content: "";
  position: absolute;
  right: -3.4rem;
  border-radius: 50%;
  width: 0.5rem;
  height: 0.5rem;
  margin-top: 4px;
  z-index: 100;
  background-color: rgba(255, 255, 255, 0.7);
}
.years .firstP::before,
.secondP::before,
.thirdP:before {
  content: "";
  position: absolute;
  right: -3.65rem;
  border-radius: 50%;
  width: 1rem;
  height: 1rem;
  z-index: 10;
  background: #ff4a57;
}

.experienceInfo h4 {
  color: white;
  font-family: "Varela Round", sans-serif;
  font-weight: 500;
  letter-spacing: 1px;
}
.experienceInfo p {
  font-size: 18px;
  color: rgba(255, 255, 255, 0.9);
}
.point {
  font-size: 16px;
}
.firstP {
  margin-bottom: 8rem;
}
.secondP {
  margin-bottom: 8rem;
}
.secondExp {
  margin: 4.2rem 0;
}

@media screen and (max-width: 576px) {
  .experience {
    padding: 0.5rem;
  }

  .col1 {
    flex-grow: 1;
  }

  .col2 {
    flex-grow: 0.3;
  }

  .col.py-5.years.mr-5.px-0.aos-init.aos-animate {
    margin-right: 1rem !important;
  }
  .line.col {
    padding: 0;
    height: 35rem;
  }
  .years .firstP::before,
  .secondP::before,
  .thirdP:before {
    right: -1.65rem;
    margin-top: 2.5rem;
  }
  .years .firstP::after,
  .secondP::after,
  .thirdP::after {
    right: -1.4rem;
    margin-top: -28px;
  }
  .secondExp {
    margin: 2.2rem 0;
  }
  .experienceDetails.row {
    max-width: 100vw;
  }
}
@media screen and (min-width: 416px) and (max-width: 576px) {
  .years .firstP::before,
  .secondP::before,
  .thirdP:before {
    margin-top: 1rem;
  }
  .secondExp {
    margin: 3.2rem 0;
  }
}

/* Blog */

.padding-up {
  padding-top: 3rem;
}

.swiper-wrapper {
  width: 350px !important;
}

.swiper-button-next,
.swiper-button-prev {
  color: #ff4a57 !important;
}

.see-all-button {
  width: 300px;
}

.single-blog {
  width: 100%;
  height: 360px;
  background-color: #272a3f;
  padding: 5%;
  border-radius: 5px;
}

.blog-img-container {
  width: 100%;
  margin-bottom: 10px;
}

.blog-img {
  transition: transform 0.7s ease-in-out;
  width: 100%;
}
.blog-img:hover {
  transform: scale(1.2);
}

.single-blog h5 {
  color: rgba(255, 255, 255, 0.9);
}

.main-button {
  background-color: transparent !important;
  color: #e0e0e0;
  border-radius: 10px;
  border: 1px solid #ff4a57;
  width: 150px;
  margin-bottom: 10px;
  transition: all 0.5s ease-in-out;
}

.main-button:hover {
  background-color: #ff4a57 !important;
}

.main-button:focus {
  outline: none;
}

@media screen and (max-width: 576px) {
  .padding-up {
    padding-top: 0;
  }

  .swiper-container {
    display: flex;
    justify-content: center;
  }
}

/* Contact */
#contact {
  padding-top: 7rem;
}
.contactHead h3 {
  font-size: 2.5rem;
  color: white;
  text-transform: uppercase;
  margin-bottom: 1rem;
}
.contactHead p {
  color: #fefefe;
}

.contact-details {
  font-family: "Exo 2", sans-serif;
  max-width: 30% !important;
  margin: 1.1rem;
  padding: 2rem !important;
  border-radius: 20px;
  box-shadow: 0px 0px 30px 0px rgba(0, 0, 0, 0.1);
}
.contact-details h4 {
  font-size: 1.4rem;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.8);
  text-transform: uppercase;
  margin: 1.5rem 0;
  letter-spacing: 0.2rem;
}
.contact-details p {
  color: white;
  letter-spacing: 0.3px;
  font-size: 1rem;
}
p.blockIcon {
  border: 1px solid #fefefe;
  margin: 0;
  padding: 0.9rem;
  border-radius: 50%;
  width: 4rem;
  height: 4rem;
}

p.blockIcon svg.svg-inline--fa.fa-home.fa-w-18,
p.blockIcon svg.svg-inline--fa.fa-phone-alt.fa-w-16,
p.blockIcon svg.svg-inline--fa.fa-envelope.fa-w-16 {
  font-size: 2rem;
  margin: 0;
  color: #ff4a57;
}

.contactForm h3 {
  color: white;
  font-size: 1rem;
}

.contactForm {
  max-width: 35rem;
  margin: 0 auto;
}
.contactForm p {
  color: white;
  letter-spacing: 1px;
  margin-bottom: 3rem;
}
.contactForm input {
  height: 3.5rem;
  background-color: #272a3f;
  border-radius: 10px;
  border: none;
}
.contactForm textarea {
  height: 7rem !important;
  background-color: #272a3f;
  border-radius: 10px;
  border: none;
}

@media screen and (max-width: 576px) {
  .contact {
    padding: 2rem;
  }
  .contact-details {
    max-width: 88% !important;
  }
}

@media screen and (min-width: 576px) and (max-width: 767px) {
  .contact-details {
    max-width: 92% !important;
  }
}
@media screen and (min-width: 768px) and (max-width: 992px) {
  .contact-details {
    margin: 0.5rem !important;
  }
}

/* Footer */
.footer-container {
  font-family: "Exo 2", sans-serif;
  align-items: center;
  width: 100%;
}

.copyRight {
  color: white;
  letter-spacing: 0.6px;
}

.myName {
  color: #ff4a57;
  font-size: 16px;
  font-weight: 700;
}

/* General */

.button-group {
  display: flex;
}
.button-group a {
  text-decoration: none;
}

.button {
  background-color: #1f2235;
  border: 1px solid #ff4a57;
  color: #fff;
  font-family: "Varela Round", sans-serif;
  font-weight: 400;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  padding: 13px 22px;
  border-radius: 6px;
  text-align: center;
  text-decoration: none;
  display: block;
  outline: none;
  margin-top: 0.6rem;
  font-size: 14px;
  letter-spacing: 1.5px;
  cursor: pointer;
  transition: all 0.3s ease-in-out 0s;
}
.button:hover {
  background-color: #ff4a57;
  color: #fff;
  transition: all 0.3s ease 0s;
  transform: translateY(-3px);
}
.button:focus {
  outline: none;
}
.button-link {
  text-decoration: none;
}

.btn {
  outline: none;
  border: none;
  color: #fff;
  text-decoration: none;
}

.btn-outline {
  outline: none;
  border: 2px solid #ff4a57;
  border-radius: 50px;
  padding: 13px 32px;
}
.btn-smart {
  border: 2px solid #ff4a57;
  border-radius: 50px;
  background: #ff4a57;
  padding: 13px 20px;
  border-radius: 50px;
  margin-left: 10px;
}

.btn-smart:hover {
  background-color: #1f2235;
}

.play {
  font-size: 25px !important;
  padding-top: 10px;
}

.heading {
  margin: 2rem 0;
  font-family: "Varela Round", sans-serif;
  color: black;
}

/* Media Queries */
/* 1150 Breakpoint */
@media screen and (max-width: 1150px) {
  .home-container {
    flex-direction: column;
  }
  .logo {
    width: 150px;
  }

  .logo img {
    width: 90%;
  }
  .content-container {
    margin-left: 0;
    margin-top: 100px;
    padding-top: 5rem;
    width: 100vw;
    align-items: center;
    justify-content: center;
    text-align: center;
  }
  .svg-container {
    width: 100vw;
  }
}

/*900 Mobile Nav Version */

@media screen and (max-width: 900px) {
  .nav-links {
    display: flex;
    flex-direction: column;
    width: 100%;
    min-height: 500px;
    position: absolute;
    top: 9vh;
    text-align: center;
    align-items: center;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
    top: 90px;
  }

  .nav-links.active {
    background: rgba(0, 0, 0, 0.9);
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
    top: 90px;
  }

  .menu-icon {
    display: block;
    position: relative;
    margin: 0 1rem;
    font-size: 1.8rem;
    cursor: pointer;
  }

  .nav-links .anchor {
    display: block;
    line-height: 9.1vh;
    width: 100vw;
